<template>
  <div class="index">
    <div class="content">{{getContent}}</div>
    <img src="../assets/app_btn.png" class="jump_btn" @click="jumpToStore">
  </div>
</template>

<script>
function getParameterByName(name) {
    // var match = RegExp('[?&]' + name + '=([^&]*)').exec(window.location.href);
    // return match && decodeURIComponent(match[1].replace(/\+/g, ' '));
    return new URL(window.location.href).searchParams.get(name)
}
export default {
  name: 'HelloWorld',
  props: {
    msg: String,
  },
  data() {
    return {
      loc: 'en'
    }
  },
  metaInfo() {
  //   <meta property="og:url" content="http://test.litatom.com:80/api/sns/v1/lit/activity/share_static">
  // <meta property="og:type" content="website">
  // <meta property="fb:app_id" content="2249012795165840">
  // <meta property="og:title" content="Litmatch">
  // <meta property="og:image" content="http://www.litatom.com/api/sns/v1/lit/image/68bd9da6-4f28-11ea-a4ab-00163e02deb4">
  // <meta property="og:description" content="I met 5 new friends on Litmatch">
    return {
      meta: [{
        property: 'og:url',
        content: 'http://test.litatom.com:80/api/sns/v1/lit/activity/share_static',
      },{
        property: 'og:type',
        content: 'website',
      },{
        property: 'fb:app_id',
        content: '2249012795165840',
      },{
        property: 'og:title',
        content: 'Litmatch',
      },{
        property: 'og:image',
        content: this.getImageMeta,
      },{
        property: 'og:description',
        content: this.getDes,
      }]
    }
  },
  mounted() {
    // console.log(this.$route.path)
    console.log(getParameterByName('loc'))
    this.loc = getParameterByName('loc')
  },
  methods: {
    jumpToStore() {
      window.location.href = 'https://play.google.com/store/apps/details?id=com.litatom.app'
    }
  },
  computed: {
    getImageMeta() {
      let url = 'http://www.litatom.com/api/sns/v1/lit/image/'
      if (this.loc === 'TH') {
        return url + '853da5b2-52c4-11ea-9e89-00163e02deb4';
      }else if (this.loc === 'VN') {
        return url + '964729c8-52c4-11ea-9e89-00163e02deb4'
      }else {
        return url + '76925d0a-52c4-11ea-9e89-00163e02deb4'
      }
    },
    getContent() {
      if (this.loc === 'TH') {
        return 'มานี่เจอคนวัยรุ่นและคนแฟชั่น'
      }else if (this.loc === 'VN') {
        return 'Gặp gỡ các bạn trẻ và thú vị tại đây'
      }else {
        return 'Meet young and Cool people here'
      }
    },
    getDes() {
      if (this.loc === 'TH') {
        return 'ฉันได้รู้จักเพื่อนใหม่5คน'
      }else if (this.loc === 'VN') {
        return 'Tôi đã gặp được 5 người bạn mới ở Litmatch'
      }else {
        return 'I met 5 new friends on Litmatch'
      }
    }
  }
}
</script>

